.homebody
{
    display: flex;
    z-index: 2;
    height: 91.5vh;
}
.features
{
    /* border:1px solid #ccc; */
    height: 91.5vh;     
    flex: 2;
    overflow:scroll;
    font-size: 1.025rem;
    font-weight: 500;
    overflow: hidden;
}
.newsfeed
{
    /* border:1px solid #ccc; */
    height: 100%;
    flex: 5;
    overflow: scroll;
}
.newsfeed::-webkit-scrollbar{
    display: none;
}
.contacts
{
    height: 100%;
    flex: 2;
    /* border:1px solid #ccc; */
    overflow: scroll;
    overflow-x: hidden;
}
.contacts::-webkit-scrollbar {
    width: 8px;
    background: transparent;
}
.contacts::-webkit-scrollbar-thumb
{
    background: #cccfcc;
    border-radius: 8px;
}
@media only screen and (max-width: 798px)
{
    .features
    {
        display: none;
    }
    .contacts
    {
        display: none;
    }
}
.newfeed-card
{
    width: 65%;
    /* border: 1px solid #ccc;  */
    background: white;
    box-shadow: 0 2px 5px 0 rgba(0,0,0,0.16), 0 2px 10px 0 rgba(0,0,0,0.12);
    height: 16vh;
    border-radius: 10px;
    margin: auto;
    margin-top: 3vh;
}
@media only screen and (max-width: 798px)
{
    .newfeed-card
    {
        width: 90%;
    }
}
.newfeed-up
{
    display: inline-block;
    width: 100%;
    padding: 1%;
}
.newfeed-user
{
    width: 15%;
    /* border: 1px solid #ccc; */
    height: 6vh;
    position: relative;
    display: inline-block;
}
.user-pic
{
    background-image: url("https://upload.wikimedia.org/wikipedia/commons/thumb/a/a7/React-icon.svg/1200px-React-icon.svg.png");
    background-position: center;
    background-size: cover;
    position: absolute;
    width: 2.25rem;
    border-radius: 50px;
    vertical-align: middle;
    height: 5.2vh;
    top:3%;
    left:20%;
    display: inline-block;
    border: 1px solid #ccc;
}
.newfeed-input
{
    /* border: 1px solid #ccc; */
    display: inline-block;
    width: 80%;
    position: relative;
    margin: 0;
    padding: 0;
    top:0;
    margin-left: 1%;
    height: 6vh;
}
.newfeed-input input
{
    width: 98%;
    height: 90%;
    border-radius: 20px;
    border: none;
    outline: none;
    background-color: #F2F3F5;
    padding-left: 1rem;
    position: absolute;

}
hr
{
    width: 98%;
    border-top: 1px solid #ccc;
    margin-left: 0;
    padding: 0;
}
.extra-feed-option
{
    display: inline-block;
    width: 30%;
    text-align: center;
    
}