body
{
    margin: 0;
    padding: 0;
}
.full-navbar
{
    padding: 0.2rem;
    width: 100%;
    height: 8vh;
    background: white;
    position: fixed;
    box-shadow: 0 2px 5px 0 rgba(0,0,0,0.16), 0 2px 10px 0 rgba(0,0,0,0.12);
    z-index: 1;
    /* border: 1px solid #ccc; */
    text-align: right;
    display: flex;
    line-height: 8vh;
}
.navbar-icon
{
    flex:2;
}
.navbar-icon input
{
    width: 74%;
    margin-left: 5%;
    height: 60%;
    vertical-align: middle;
    border-radius: 20px;
    border: none;
    background: #F2F3F5;
    padding: 0.25rem;
    font-size: 1.15rem;
}
.facebook-image
{
    width:15%;
    margin-left: 2%;
    display: inline-block;
    border-radius: 50px;
    height:90%;
}
.facebook-image img{
    border-radius: 50px;
    vertical-align: middle;
    width: 100%;
    height: 100%;
}

.navbar-activity
{
    flex:5;
}
@media only screen and (max-width: 798px)
{
    .navbar-activity
    {
        display: none;
    }
}
.navbar
{
    flex: 2;
}
.navbar a
{
    text-decoration: none;
    margin-right: 1rem; 
    font-size: 1.25rem;
}
.navbar-circle
{
    display: inline-block;
    border-radius: 50px;
    width: 13.5%;
    text-align: center;
    vertical-align: middle;
    height: 75%;
    background: #F2F3F5;
}
.navbar-circle i
{
    font-size: 1.35rem;
    margin-top: 25%;
    vertical-align: top;
}
.createShadow
{
    z-index: 1;
    width: 100%;
    height: 100vh;
    text-align: start;
    position: absolute;
    margin-top: 8vh;
    /* background: rgba(0,0,0,0.1); */
}
.option-menus
{
    margin: auto;
    display: flex;
    width: 70%;
}
.op-menu
{
    /* border-left: 1px solid #ccc;
    border-right: 1px solid #ccc; */
    text-align: center;
    flex: 1;
}

.op-menu:hover
{
    background: rgba(0,0,0,0.1);
    border-radius: 10px;
    color: blue;
}