.newcard-user
{
    width: 65%;
    /* border: 1px solid #ccc;  */
    background: white;
    box-shadow: 0 2px 5px 0 rgba(0,0,0,0.16), 0 2px 10px 0 rgba(0,0,0,0.12);
    border-radius: 10px;
    margin: auto;
    margin-top: 3vh;
    position: relative;
}
@media only screen and (max-width: 798px)
{
    .newcard-user
    {
        width: 90%;
    }
}
.name-field
{
    width:98%;
    font-weight: bold;
    font-size: 1.25rem;
    padding: 1%;
    vertical-align: middle;
    display: inline-block;
    height: 8vh;
    /* border:1px solid #ccc; */
}
.user-photo
{
    background-image: url("https://upload.wikimedia.org/wikipedia/commons/thumb/a/a7/React-icon.svg/1200px-React-icon.svg.png");
    background-position: center;
    background-size: cover;
    position: absolute;
    width: 7%;
    border-radius: 50px;
    vertical-align: middle;
    height: 5.2vh;
    top:3%;
    display: inline-block;
    border: 1px solid #ccc;
}
.user-name
{
    margin-left: 10%;
    display: inline-block;
    height: 8vh;
    width: 80%;
}
.photo-status
{
    padding:2%;
    width: 96%;
    /* border:1px solid #ccc;    */
}
.user-upload-photo
{
    width: 100%;
    height: 50vh;
    /* border:1px solid #ccc; */
}
.user-upload-photo img{
    width: 100%;
    height: 100%;
}
.engagement
{
    width: 96%;
    padding: 2%;
    /* border:1px solid #ccc; */
    display: inline-block;
}
.ldc
{
    width: 33%;
    display: inline-block;
    text-align: center;
}