ul
{
    list-style: none;
    margin: 0;
    /* padding: 1.25rem; */
    padding-top: 1.25rem;
    padding-bottom: 1.25rem;
    padding-left: 1rem;
    padding-right: 0;
}
li
{
    /* padding: 1.25rem; */
    padding-top: 1rem;
    padding-bottom: 1rem;
    padding-left: 0.5rem;
    padding-right: 0;
    border-radius: 10px 0 0 10px;
}
li i
{
    font-size: 1.25rem;
    margin-right: 1rem;
}
li:hover
{
    background: rgba(0,0,0,0.1);
}
.scrollfea
{
    height: 100%;
    overflow: hidden;
}
.scrollfea::-webkit-scrollbar
{
    width: 8px;
    background: transparent;
}
.scrollfea::-webkit-scrollbar-thumb
{
    background: gray;
    border-radius: 8px;
}
.scrollfea:hover{
    overflow: scroll;
    overflow-x: hidden;
}