.contact
{
    display: inline-block;
    width: 100%;
    position: relative;
    padding: 1rem;
    height: 5vh;
    /* vertical-align: middle; */
}
.user-pics
{
    background-image: url("https://www.pinclipart.com/picdir/middle/127-1274125_archive-computer-engineering-development-folder-web-developer-icon.png");
    background-position: center;
    background-size: cover;
    width: 10%;
    position: absolute;
    vertical-align: middle;
    border: 1px solid #ccc;
    border-radius: 50px;
    height: 5vh;
    display: inline-block;
}
.user-names
{
    margin-left: 14%;
    display: inline-block;
    width: 80%;
    height: 5vh;
    font-size: 1.25rem;
    font-weight: 600;
}