.GLanding
{
    display: flex;
    height: 91.5vh;
    width: 100%;;
}
.added-group
{
    flex: 1;
    height: 91.5vh;
    /* border: 1px solid black; */
    background: white;
    box-shadow: 0 2px 5px 0 rgba(0,0,0,0.16), 0 2px 10px 0 rgba(0,0,0,0.12);
}
.added-group-content
{
    padding: 1rem;
}
.new-group-search
{
    width: 90%;
    margin: auto;
    border:none;
    font-size: 1.25rem;
    padding: 0.5rem;
    background: #F2F3F5;
    border-radius: 20px;
    margin-top: 1rem;
}
.new-group-submit
{
    background: #b3d9ff;
    color: blue;
    width: 95%;
    margin: auto;
    border:none;
    font-size: 1.25rem;
    padding: 0.5rem;
    border-radius: 10px;
    margin-top: 1rem;
}
.your-groups
{
    padding:1rem;
}
.list-groups
{
    overflow: scroll;
    flex: 3;
    height: 91.5vh;
}
.list-groups::-webkit-scrollbar
{
    width: 0;
}
.friend-group
{
    padding: 2rem;
    padding-left: 4rem;
}

.group-card
{
    margin-top: 2rem;
    width: 96%;
    height: 55vh;
    border-radius: 20px;
    background: white;
    box-shadow: 0 2px 5px 0 rgba(0,0,0,0.16), 0 2px 10px 0 rgba(0,0,0,0.12);
}
.group-card-body
{
    padding: 1rem;
    color: gray;
}
.group-card-image
{
    width: 100%;
    height: 65%;
}
.group-card-body input
{
    color: gray;
    width: 100%;
    background-color: #cccccc;
    border: none;
    border-radius: 10px;
    margin: auto;
    padding: 0.6rem;
    font-size: 1.25rem;
    margin-top: 1vh;
}
.group-card-image img
{
    border-radius: 20px 20px 0 0;
    width: 100%;    
    height: 100%;
    filter: grayscale(50%);
}
.group-card-image img:hover
{
    filter: none;
}
.adding-card
{
    display: flex;
    width: 100%;
}
.card-1
{
    flex: 1;
}
.card-2
{
    flex: 1;
}