.mainDiv
{
    position: absolute;
    margin-left: auto;
    margin-right: auto;
    top: 18vh;
    left: 0;
    bottom: 0;
    padding: 1rem;
    right: 0;
    width: 35%;
    box-shadow: 0 2px 5px 0 rgba(0,0,0,0.16), 0 2px 10px 0 rgba(0,0,0,0.12);
    background: white;
    height: 68vh;
    border-radius: 10px;
    text-align: start;
    z-index: 2;
}
@media only screen and (max-width: 798px)
{
    .mainDiv
    {
        width: 80%;
    }
}

.notiDiv
{
    text-align: start;
    position: absolute;
    right:2%;
    top: 9vh;
    box-shadow: 0 2px 5px 0 rgba(0,0,0,0.16), 0 2px 10px 0 rgba(0,0,0,0.12);
    background: white;
    border-radius: 10px;
    width: 25%;
    height: 80vh;
    z-index: 2;
}
@media only screen and (max-width: 798px)
{
    .notiDiv
    {
        width: 70%;
    }
}
.heading
{
    font-size: 1.25rem;
    /* vertical-align: middle; */
    padding: 0.5rem;
    font-weight: 700;
    text-align:center;
    border-bottom: 1px solid #ccc;
}
.name-photo
{
    padding: 0.5rem;
    position: relative;
}
.user-photoss
{
    background-image: url("https://upload.wikimedia.org/wikipedia/commons/thumb/a/a7/React-icon.svg/1200px-React-icon.svg.png");
    background-position: center;
    background-size: cover;
    position: absolute;
    width: 7%;
    border-radius: 50px;
    vertical-align: middle;
    height: 5.2vh;
    top:20%;
    display: inline-block;
    border: 1px solid #ccc;
}
.posttextarea
{
    width: 100%;
    height: 30vh;
}
.posttextarea textarea
{
    width: 100%;
    font-size: 2rem;
    height: 30vh;
    resize: none;
    border: none;
}
.posttextarea textarea::-webkit-scrollbar{
    display: none;
}
.addextra
{
    border: 1px solid #ccc;
    width: 100%;
    margin-top: 1vh;
    height: 8vh;
    border-radius: 10px;
}
.submitpost
{
    margin-top: 1vh;
    width: 100%;
}
.submitpost input{
    border: none;
    color: gray;
    padding: 0.4rem;
    width: 100%;
    border-radius: 10px;
    font-size: 1.25rem;
}